body{
    margin:0;
    padding:0;
}

.navi{

    background-color: #171717; 
}
.navi-content{
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin:0 auto;
    max-width: 1100px;
    font-family: 'OSWALD';
}
.navi-content>h3{
    color:#dfdfdf;
    font-size:20px;
    padding:10px;
    letter-spacing: 6px;
}

.navi-content>nav{
    letter-spacing: 2px;
    color:#c1c1c1;
}

.navi-content>nav>a{
    text-decoration: none;
    color:inherit;
    padding:10px;
}
.navi-content>nav>a:hover{
    color:#eceaea;
    transition-duration: 0.6s;
}

/* First container */
.start{
    font-family: 'Oswald';
    background-color: #e9e9e9;
    display:flex;
    align-items: center;
    justify-content:center;
    flex-wrap:wrap;
    min-height: 568px
}

.containter{
    margin-bottom: 175px ;
}

.face-box {
    position: absolute;
    box-shadow: inset 0 0 0 3px #149df2;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;

}

.containter>h1{
    max-width: 630px;
    text-align: left;
    letter-spacing: 5px;
    font-size: 40px;
    line-height: 32px;
    margin-bottom: 20px;

}
.containter>h2 {
    max-width: 400px;
    text-align: left;
    letter-spacing: 1px;
    font-size: 19px;
    line-height: 32px;
    margin-bottom: 20px;
}

.selfie-vector {
    margin-top:20px
}

.search {
    -webkit-appearance: none;
    border: solid 1px;
    outline: none;
    padding: 10px;
    width: 450px;
    line-height: 30px;
    margin-bottom: 20px;
  }
  
.underline {
    display:flex;
    justify-content: space-between;
    align-items: center;
}
.underline>div{
    display: flex;
    align-items: center;
}

button {
    background-color:#171717;
    color:#dfdfdf;
    font-family: 'OSWALD';
    font-size: 15px;
    border:none;
    padding: 10px 32px;
    transition-duration: 0.6s;
}

button:hover{
    cursor: pointer;
    background-color: rgb(108, 99, 255);
}

/*Second container*/

.content{
    font-family: 'Oswald';
    margin:auto;
    width:50%;
    min-height: 300px;
}

.content>div{
    display:flex;
    flex-direction: column;
    align-items: center;
}

.content2{
    font-family: 'Oswald';
    margin:auto;
    width:100%;
    min-height: 300px;
    background-color: #e9e9e9;
}

.content2>div{
    display:flex;
    flex-direction: column;
    align-items: center;
}

.projects-container {
    display: flex;
}
.projects-container>img {
    padding: 10px 40px
}



/* Footer */

.footer{
    background-color: #171717;

}

.footer-container{
    color:#c1c1c1;
    font-family: 'OSWALD';
    max-width: 1100px;
    margin:0 auto;
    padding: 20px 10px 10px;
}
.upper-footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: #464646 solid 1px;
}
.upper-footer>h3{
    color:#dfdfdf;
    letter-spacing: 6px;
}
.upper-footer>nav>a{
    letter-spacing: 2px;
    padding:10px;
    text-decoration: none;
    color:inherit;
}
.upper-footer>nav>a:hover{
    padding:10px;
    letter-spacing: 2px;
    color:#eceaea;
    transition-duration: 0.6s;
}

.lower-footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    letter-spacing: 2px;

}
.lower-footer>nav>a{
    padding:10px;
    letter-spacing: 2px;
}

